.footer {
  height: 100vh;
}

.right {
  display: none;
}

.page-footer {
  height: 50vh;
  box-sizing: border-box;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 500;
  font-size: 18px;
  margin-top: 12%;

  @media (max-width: 768px) {
    padding-top: 50px;
  }
}

.logo {
  margin: 0;
  color: $white;
  border-bottom: 4px solid #ffffff;
  width: 160px;
  height: 55px;
  overflow: hidden;
  position: relative;

  &--footer {
    margin-bottom: 90px;
    border-color: #000000;

    @media (max-width: 768px) {
      margin-bottom: 30px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    font-size: 56px;
    line-height: 100%;
    width: 115px;
    position: absolute;
    left: 0;
    bottom: 0;

    &--footer {
      color: #000000;

    }
  }
}

.contacts {
  display: flex;
  align-items: center;

  @media (max-width: 768px) {
    display: block;
  }

  &__title {
    margin-right: 10px;
    font-size: 20px;
    text-transform: uppercase;

    @media (max-width: 768px) {
      text-align: center;
    }
  }

  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;

    @media (max-width: 768px) {
      flex-direction: column;
      align-items: center;
    }
  }

  li {
    @media (max-width: 768px) {
      margin-bottom: 10px;
    }
  }

  &__link {
    position: relative;
    margin-right: 10px;
    font-family: inherit;
    font-size: 20px;
    font-weight: 300;
    line-height: 120%;
    text-transform: uppercase;
    color: #000000;
    opacity: 1;
    transition: 200ms;


    &:hover {
      opacity: 0.6;
      transition: 200ms;
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -2px;
      height: 1px;
      width: 0px;
      background-color: #000000;
      transition: 300ms;
    }

    &:hover::after {
      width: 100%;
      animation: contactLink 300ms linear;
    }
  }
}

@keyframes contactLink {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}


@keyframes sectionTitleOn {
  100% {
    letter-spacing: 2px;
    opacity: 0.6;
  }
}

@keyframes sectionTitleOff {
  0% {
    letter-spacing: 2px;
    opacity: 0.6;
    // transition: 1s;
  }

  100% {
    letter-spacing: 0px;
    opacity: 1;
    // transition: 1s;
  }
}
